.App {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.left {
  display: flex;
  flex-direction: column;
  width: 25vw;
  gap: 10px;
  padding: 10px;

  .row {
    display: flex;
    flex-direction: row;

    .rowNumber {
      width: 45px;
      align-content: center;
      font-weight: 500;
    }

    .selectedNumber {
      cursor: pointer;
      min-width: 50px;

      background-color: #E7D4B5;
      border-radius: 1em;
      padding: 2px;

      text-align: center;
      color: #A0937D;
      font-weight: 700;
    }
    .selectedNumber:hover {
      background-color: #ECDBBA;
    }
  }
}

.right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50vw;

  padding: 10px;
  font-weight: 700;

  .number {
    font-size: 8em;
    color: #ECDBBA;
  }

  button {
    padding: 10px;
    border-radius: 10px;
    font-size: 1em;
    background-color: #B6C7AA;
    color: #A0937D;
    border: solid 1px #F6E6CB;
  }
}

.error {
  color: #B6C7AA;
  text-align: center;
}

.strikethrough {
  text-decoration: line-through;
  color: #B6C7AA;
  display: inherit;
}

@media (prefers-color-scheme: dark) {
  .right {
    button {
      background-color: #C84B31;
      color: #ECDBBA;
      border: solid 1px #C84B31;
      font-weight: 500;
    }
  }

.left {
  .row {
    .selectedNumber {
      background-color: #2D4263;
    }
    .selectedNumber:hover {
      background-color: #425066;
    }
  }
}
  .error {
    color: #C84B31;
  }

  .strikethrough {
    color: #C84B31;
  }
}


@media (device-width < 480px) {
  .left {
    width: 25vw;
  }
  .right {
    width: 75vw;
    .number {
      font-size: 4em;
    }
  }

  .error {
    text-align: center;
  }
}